export default {
    downloadToCSV(data) {
        let csvContentFileHeader = "data:text/csv;charset=utf-8,%EF%BB%BF" 
        let csvContentBody = "Path;Acción;Antes;Después;\n" + data.map(e => `"${e.path}";"${e.action}";${e.before};${e.after};`).join("\n");
        var encodedUri = csvContentFileHeader + encodeURIComponent(csvContentBody);
        var link = document.createElement("a");
        link.setAttribute("href", encodedUri);
        link.setAttribute("download", "compare.csv");
        document.body.appendChild(link);
        link.click();
    }
}